.left {
    position: relative;
    height: 100%;
    width: 50%;
    left: 0%;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-transition: left 0.4s ease-in;
    -o-transition: left 0.4s ease-in;
    transition: left 0.4s ease-in;
}

.left.closed {
    left: -50%;
}

.left .playing-next {
    margin-top: 100px;
    height: 120px;
}

.left .cover-art {
    width: 120px;
    height: 120px;
    background-color: green;
    margin-left: 60px;
}

.left .playing-next-header {
    color: white;
    margin: 0;
    font-size: 40px;
}

.left .playing-next-title {

}

.left .playing-next-author {
    
}

.left .playing-next {
    display: -ms-flexbox;
    display: flex;
    padding: 25px;
}

.left .playing-next-info {
    padding-left: 10px
}


