.search-container {
  position: absolute;
  height: 24px;
  width: 300px;
  top: 10px;
  right: 120px;
}

.search-container .search-logo {
  position: absolute;
  right: 0px;
}

.search-container .search-box {
  border: none;
  border-bottom: 1px solid #AEB0B1;
  border-radius: 0px;
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 14px;
  height: 18px;
  margin-top: 0.7vmin;
  outline: none;
  padding-bottom: 8px;
  padding-top: 0px;
  width: 100%;
  -webkit-appearance: none;
  color: #909090;
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-size: 16px;
  float: right;
  margin-right: 24px;
  -webkit-transition: width 0.4s ease-in;
  -o-transition: width 0.4s ease-in;
  transition: width 0.4s ease-in;
}

.search-container .close {
  width: 0px;
}


