.right {
    position: absolute;
    height: 100%;
    width: 50%;
    right: 0;
    -webkit-transition: width 0.4s ease-in;
    -o-transition: width 0.4s ease-in;
    transition: width 0.4s ease-in;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.right.closed {
    width: 100%;
}

.right .right-container {
    width: 400px;
    height: 400px;
    background-color: black;
    -webkit-transition: width 0.4s ease-in;
    -o-transition: width 0.4s ease-in;
    transition: width 0.4s ease-in;
}

.right .center {
    right: 35%;
}


